.card {
    @include flex(space-between, start);
    max-height: 1000px;
    gap: 3%;
    padding: 3rem 5px;
    @include media-mobile {
        flex-direction: column;
        max-height: fit-content;
    }

    &__pic {
        max-width: 66rem;
        max-height: 47rem;
        img {
            object-fit: contain;
            max-width: 100%;
            max-height: 100%;
        }
    }
    &__header {
        padding-bottom: 2%;
    }
    &__name {
        @include font(3rem, 4.5rem, 600);
        @include media-mobile {
            font-size: 2rem;
        }
    }
    &__weight {
        @include font(1.2rem, 1.6rem, 400);
        display: flex;
        text-align: center;
        gap: 1rem;
        color: $main-text;
    }
    &__price {
        @include font(3rem, 3.9rem, 800);
        @include media-mobile {
            font-size: 2rem;
            width: 25%;
        }
    }
    &__body {
        @include flex(space-between, start, column);
        gap: 2rem;
    }
    &__country,  &__brand {
        @include font(1.4rem, 2rem, 300);
        color: $main-text;
        :last-child {
            @include font(1.4rem, 2rem, 500);
        }
    }

    &-disclaim {
        &__title {
            @include font(1.6rem, 2.4rem, 500);
        }
        &__text {
            @include font(1.2rem, 1.8rem, 300);
            color: $main-text;
        }
    }
    &-charact {
        &__title {
            @include font(1.6rem, 2.4rem, 500); 
        }
        &__item {
            @include font(1.4rem, 2.1rem, 300);
            color: $main-text;

            span {
                @include font(1.4rem, 2.1rem, 500);
            }
        }
    }
    &__actions {
        margin: 2rem auto;
        display: flex;
        align-items: center;
        gap: 2rem;
    }
    &__manage-btn {
        padding: 0 4rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}